.Slide_slideModule__360R_{
    height: 800px;
    width: 100%;
    background-image: url("https://images.pexels.com/photos/262367/pexels-photo-262367.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
    background-size: cover;
}

.Slide_slideModule__360R_::after{
    content: "";
    position: absolute;
    width: 100%;
    height: 800px;
    top: 0;
    left: 0;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(20%, rgba(0, 242, 255, .2)), to(#333));
    background: -webkit-linear-gradient(top, rgba(0, 242, 255, .2) 20%, #333);
    background: linear-gradient(to bottom, rgba(0, 242, 255, .2) 20%, #333);
}

